import { ITableColumnDef } from '../models/searchTableModel';

/** tableColumnTypes: an object for generic column types that can be re-used throughout
 * the table implementations. If you find you're reusing the same (multiple) settings,
 * consider making a type for that here. */
export const tableColumnTypes: { [key: string]: ITableColumnDef } = {
    disableSort: {
        suppressMenu: true,
        sortable: false,
    },
};

// Use these for generic column fields & header translation keys (e.g. 'DELETE')
export enum ColumnHeader {
    DELETE = 'DELETE',
    ACTION = 'ACTION',
}
export enum ColumnField {
    DELETE = 'delete',
    ACTION = 'action',
}

export interface ColumnCreatorProperties extends ITableColumnDef {
    headerKey: string;
}
